import axios from "axios"
import React, {useState, useEffect} from 'react'



export default function Display() {
    const [input, setInput] = useState("");
    const [products, setProducts] = useState([])
    
    const fetchingProduct = () => {
        axios.get("http://localhost:4004/api/products")
        .then((result) => {
            setProducts(result.data)
        })
        .catch((e) => console.log(e.message))
    }
    useEffect(fetchingProduct, [] )
    


    const submitHandler = (event) => {
        event.preventDefault();
        axios.post("http://localhost:4004/api/save", {input})
        .then(() => fetchingProduct());
        setInput("")
    }

    const handleDone = (_id) => {
        axios.put(`http://localhost:4004/api/products/${_id}`)
        .then(() => console.log(_id))
        .then(() => fetchingProduct() )
        .catch((e) => console.log(e.message))
        
    }
    


    const handleDelete = (_id) => {
        axios.delete(`http://localhost:4004/api/products/${_id}`)
        .then(() => console.log(`product with following ${_id} was deleted`))
        .then(() => fetchingProduct())
        .catch((e) => console.log(e.message))
    }

    
    return(
        <div>
            <form onSubmit={submitHandler}>
            <input type="text" className="products-input"  value={input} onChange={(event) => setInput(event.target.value)}/>
            <button type="submit" className="submit-button">Add a product</button>
        </form>
                  
        
            {products.map((product) => (
                    <li  key={product._id}>
                    <input className={`${product.done? "done": ""}`} type="text" value={product.item} />
                  
                        <button onClick={() => handleDone(product._id)}>
                            <i className="far fa-check-square"></i>
                        </button>
                        <button onClick={() => handleDelete(product._id)}>
                            <i className="far fa-trash-alt"></i>
                        </button>
                        <div>
                       
                </div>
                    
                </li>
            ))}
            
        </div>
    )
}


